import React from "react";
import { UncontrolledCollapse, Row, Col } from "reactstrap";

const DiagnosticoSistemasAgrarios = ({ diagnosisAgriculturalSystem }) => {
  return (
    <>
      <h3
        className="border rounded bg-default text-center text-white py-2 w-100"
      >
        Diagnstico de Sistemas Agrrios
      </h3>
      <div>
          <Row>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Qual  a renda extra lote (fora do lote) anual?:</b>{" "}
                {diagnosisAgriculturalSystem.income_off_lot}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>
                  A famlia recebe algum tipo de aux. de programa social
                  governamental?:
                </b>{" "}
                {diagnosisAgriculturalSystem.government_assistance ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>A famlia acessou alguma poltica pblica para moradia?:</b>{" "}
                {diagnosisAgriculturalSystem.housing_policy ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>
                  Acessou alguma linha de financiamento para projetos
                  desenvolvidos no lote?:
                </b>{" "}
                {diagnosisAgriculturalSystem.financing_line ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Tem meio de comunicao rural?:</b>{" "}
                {diagnosisAgriculturalSystem.has_rural_communication ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Comunicao rural - Tipo:</b>{" "}
                {diagnosisAgriculturalSystem.rural_communication}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Destino final do lixo seco:</b>{" "}
                {diagnosisAgriculturalSystem.final_destination_waste}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Saneamento bsico da moradia (esgoto sanitrio):</b>{" "}
                {diagnosisAgriculturalSystem.has_basic_sanitation ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Principal meio de transporte escolar:</b>{" "}
                {diagnosisAgriculturalSystem.schools_transport}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Contrata empregados para ajudar na produo do lote?:</b>{" "}
                {diagnosisAgriculturalSystem.hire_employees ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Quantos empregados permanentes?:</b>{" "}
                {diagnosisAgriculturalSystem.fixed_employees}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Recebeu assistncia tcnica na ltima safra/ano?:</b>{" "}
                {diagnosisAgriculturalSystem.technical_assistance
                  ? "Sim"
                  : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>
                  Lembra da ltima queimada descontrolada que atingiu o lote?:
                </b>{" "}
                {diagnosisAgriculturalSystem.reminds_burning_in_lot
                  ? "Sim"
                  : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Ano dessa queimada:</b>{" "}
                {diagnosisAgriculturalSystem.year_burning}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>O lote tem acesso a gua?:</b>{" "}
                {diagnosisAgriculturalSystem.has_water_access ? "Sim" : "No"}
              </div>
            </Col>
          </Row>
          <Row>
            <Col
              lg="12"
              className="bg-default rounded d-flex justify-content-center mb-2"
            >
              <h3 className="text-white my-2">Fornecimento de energia</h3>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Tem eletrificao?:</b>{" "}
                {diagnosisAgriculturalSystem.has_energy ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Tipo de rede:</b> {diagnosisAgriculturalSystem.network_type}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Energia disponvel atende  produo?:</b>{" "}
                {diagnosisAgriculturalSystem.energy_meets_production
                  ? "Sim"
                  : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Complementa o fornecimento de energia?:</b>{" "}
                {diagnosisAgriculturalSystem.complements_energy ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Utiliza, tambm, algum desses?:</b>{" "}
                {diagnosisAgriculturalSystem.uses_any_these}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Deseja obter / complementar?:</b>{" "}
                {diagnosisAgriculturalSystem.want_to_get ? "Sim" : "No"}
              </div>
            </Col>
          </Row>
          <Row>
            <Col
              lg="12"
              className="bg-default rounded d-flex justify-content-center mb-2"
            >
              <h3 className="text-white my-2">gua Produo</h3>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Lote tem gua tratada?:</b>{" "}
                {diagnosisAgriculturalSystem.potable_water ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Lote tem acesso a gua o ano todo?:</b>{" "}
                {diagnosisAgriculturalSystem.year_water_access ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>H fonte de gua natural no lote?:</b>{" "}
                {diagnosisAgriculturalSystem.there_is_natural_water_source
                  ? "Sim"
                  : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>
                  As fontes de gua natural no lote so protegidas por mata
                  ciliar?:
                </b>{" "}
                {diagnosisAgriculturalSystem.riparian_forest ? "Sim" : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>
                  Qual a distncia entre o local de captao da fonte de gua
                  natural para sua moradia e elementos contaminantes? (m):
                </b>{" "}
                {diagnosisAgriculturalSystem.distance_water_intake}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>
                  A captao levou  alterao do curso natural do crrego da
                  fonte?:
                </b>{" "}
                {diagnosisAgriculturalSystem.alteration_in_the_watercourse
                  ? "Sim"
                  : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>
                  H captao de gua de chuva para reaproveitamento no lote?:
                </b>{" "}
                {diagnosisAgriculturalSystem.captures_rain_water
                  ? "Sim"
                  : "No"}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Como  utilizada a gua captada da chuva?:</b>{" "}
                {diagnosisAgriculturalSystem.how_uses_rainwater}
              </div>
            </Col>
            <Col lg="6" className="mb-3">
              <div className="border border-default rounded p-2">
                <b>Alteraes no curso natural do crrego da fonte?:</b>{" "}
                {diagnosisAgriculturalSystem.alteration_water_course ? "Sim"  : "No"}
              </div>
            </Col>
          </Row>
      </div>
    </>
  );
};

export default DiagnosticoSistemasAgrarios;
