import React, { useState } from "react";
import { Col, FormGroup, Input, Row } from "reactstrap";
import { mask, unMask } from "remask";
import { findZipCode } from "utils/findZipCode";
import { cpfValidator } from "utils/validators";
import { clearInput } from "utils/validators";

const Member = ({ inputMember, setInputMember, usersAffiliation }) => {
  const [invalidInput, setInvalidInput] = useState(false);

  const handleChangeInputZipCode = (res) => {
    setInputMember({
      ...inputMember,
      city: res.city,
      state: res.state,
      address: res.street,
    });
  };

  const handleChangeInput = (event) => {
    const { name, value } = event.target;
    setInputMember({ ...inputMember, [name]: value });
  };

  return (
    <Row>
      <Col lg="12" className="border p-4 mb-3 shadow rounded">
        <Row>
          <Col lg="12">
            <h2 className="text-center mb-3">Dados do Beneficirio</h2>
            <hr/>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="affiliation">
                Afiliao <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                type="select"
                id="affiliation"
                name="affiliation"
                onChange={handleChangeInput}
                value={inputMember.affiliation}
                required
              >
                <option value="" hidden>
                  Escolha uma opo
                </option>
                {usersAffiliation.map((afilliate, i) => (
                  <option key={i} value={afilliate.affiliation}>
                    {afilliate.name}
                  </option>
                ))}
              </Input>
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="name">
                Nome completo <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="name"
                id="name"
                title="Digite seu nome completo"
                placeholder="Ex: Joo da Silva"
                value={inputMember.name}
                onChange={handleChangeInput}
                maxLength="255"
                minLength="1"
                required
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="cpf">
                CPF <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="cpf"
                value={mask(inputMember.cpf, ["999.999.999-99"])}
                placeholder="123.567.112-99"
                onChange={(e) => {
                  handleChangeInput(e);
                  setInvalidInput(cpfValidator(e));
                }}
                onBlur={(e) => {
                  setInvalidInput(clearInput(e, cpfValidator(e)));
                }}
                title="CPF"
                maxLength="14"
                minLength="1"
                required
              />
              {invalidInput && (
                <small className="ml-2 text-red">
                  O nmero do CPF est incorreto. Por favor, tente novamente.
                </small>
              )}
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="rg">
                RG
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="rg"
                title="RG"
                placeholder="Ex: 0000000"
                value={inputMember.rg}
                onChange={handleChangeInput}
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="nis">
                NIS
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="nis"
                title="NIS"
                placeholder="Ex: 0000000"
                value={inputMember.nis}
                maxLength="14"
                onChange={handleChangeInput}
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="email">
                Email
              </label>
              <Input
                className="form-control-alternative"
                type="email"
                name="email"
                title="Email"
                placeholder="Ex: email@conafer.org.br"
                value={inputMember.email}
                onChange={handleChangeInput}
                maxLength="254"
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="phone">
                Telefone <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                className="form-control-alternative"
                type="tel"
                name="phone"
                title="Telefone"
                placeholder="Ex: (00) 00000-0000"
                value={mask(inputMember.phone, ["(99) 99999-9999"])}
                onChange={handleChangeInput}
                maxLength="17"
                minLength="1"
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="alternative_phone">
                Telefone Alternativo
              </label>
              <Input
                className="form-control-alternative"
                type="tel"
                name="alternative_phone"
                title="Telefone Alternativo"
                placeholder="Ex: (00) 00000-0000"
                value={mask(inputMember.alternative_phone, ["(99) 99999-9999"])}
                onChange={handleChangeInput}
                maxLength="17"
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="collection_code">
                Cdigo de Coleta
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="collection_code"
                title="Cdigo de Coleta"
                placeholder="Ex: 0000"
                value={inputMember.collection_code}
                onChange={handleChangeInput}
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="citizenship">
                Naturalidade
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="citizenship"
                title="Naturalidade"
                placeholder="Ex: Macei - AL"
                value={inputMember.citizenship}
                onChange={handleChangeInput}
                maxLength="30"
                minLength="1"
                required
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="rb_status">
                Status na RB
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="rb_status"
                title="Status na RB"
                placeholder="Ex: Titulado"
                value={inputMember.rb_status}
                onChange={handleChangeInput}
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="incra_area">
                Ocupa rea destinada pelo Incra{" "}
                <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                className="form-control-alternative"
                type="select"
                name="incra_area"
                title="Ocupa rea destinada pelo Incra"
                placeholder="Ocupa rea destinada pelo Incra"
                value={inputMember.incra_area}
                onChange={handleChangeInput}
                required
              >
                <option value={undefined} hidden>
                  Escolha uma opo
                </option>
                <option value={true}>Sim</option>
                <option value={false}>No</option>
              </Input>
            </FormGroup>
          </Col>
        </Row>
      </Col>
      <Col lg="12" className="border p-4 mb-3 shadow rounded">
        <Row>
          <Col lg="12">
            <h2 className="text-center mb-3">
              Idenificao da Unidade Familiar
            </h2>
            <hr/>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="status">
                Status
              </label>
              <Input
                className="form-control-alternative"
                type="status"
                name="status"
                title="Status"
                placeholder="Ex: Beneficirio"
                value={inputMember.status}
                onChange={handleChangeInput}
              />
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="marital_status">
                Estado Civil <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                className="form-control-alternative"
                type="select"
                name="marital_status"
                title="Estado Civil"
                placeholder="Ex: Solteiro"
                value={inputMember.marital_status}
                onChange={handleChangeInput}
                required
              >
                <option value="" hidden>
                  Escolha uma opo
                </option>
                <option value="Solteiro(a)">Solteiro(a)</option>
                <option value="Unio Estvel">Unio Estvel</option>
                <option value="Casado(a)">Casado(a)</option>
                <option value="Divorciado(a)">Divorciado(a)</option>
                <option value="Vivo(a)">Vivo(a)</option>
              </Input>
            </FormGroup>
          </Col>
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="mother_name">
                Nome da Me <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="mother_name"
                title="Nome da Me"
                placeholder="Ex: Maria da Silva"
                value={inputMember.mother_name}
                onChange={handleChangeInput}
                maxLength="255"
                minLength="1"
                required
              />
            </FormGroup>
          </Col>
          {(inputMember.marital_status === "Unio Estvel" ||
            inputMember.marital_status === "Casado(a)") && (
            <>
              <Col lg="6">
                <FormGroup>
                  <label className="form-control-label" htmlFor="spouse_name">
                    Nome do Cnjuge
                  </label>
                  <Input
                    className="form-control-alternative"
                    type="text"
                    name="spouse_name"
                    title="Nome do Cnjuge"
                    placeholder="Ex: Fernanda da Silva"
                    value={inputMember.spouse_name}
                    onChange={handleChangeInput}
                    required
                  />
                </FormGroup>
              </Col>
              <Col lg="6">
                <FormGroup>
                  <label className="form-control-label" htmlFor="spouse_cpf">
                    CPF do Cnjuge
                  </label>
                  <Input
                    className="form-control-alternative"
                    type="text"
                    name="spouse_cpf"
                    title="CPF do Cnjuge"
                    placeholder="Ex: 123.567.112-99"
                    value={mask(unMask(inputMember.spouse_cpf), [
                      "999.999.999-99",
                    ])}
                    onChange={handleChangeInput}
                    maxLength="14"
                    required
                  />
                </FormGroup>
              </Col>
            </>
          )}
          <Col lg="6">
            <FormGroup>
              <label className="form-control-label" htmlFor="nationality">
                Nacionalidade <small className="text-red">(obrigatrio)</small>
              </label>
              <Input
                className="form-control-alternative"
                type="text"
                name="nationality"
                title="Nacionalidade"
                placeholder="Ex: Brasileiro"
                value={inputMember.nationality}
                maxLength="30"
                minLength="1"
                onChange={handleChangeInput}
              />
            </FormGroup>
          </Col>
        </Row>
      </Col>
      <Col lg="12" className="border p-4 mb-3 shadow rounded">
      <Row>
      <Col lg="12">
        <h2 className="text-center mb-3">
          Informaes Gerais da Unidade Familiar
        </h2>
        <hr/>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="operational_core">
            Ncleo Operacional da ATER (Municpio)
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="operational_core"
            title="Ncleo Operacional da ATER (Municpio)"
            placeholder="Ex: Seaprof"
            value={inputMember.operational_core}
            onChange={handleChangeInput}
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="location_zone">
            Zona de Localizao
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="location_zone"
            title="Zona de Localizao"
            placeholder="Ex: Zona Rural"
            value={inputMember.location_zone}
            onChange={handleChangeInput}
            maxLength="17"
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="cep">
            CEP <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="cep"
            title="CEP"
            placeholder="Ex: 57.160-000"
            value={mask(inputMember.cep, ["99.999-999"])}
            onChange={handleChangeInput}
            onBlur={async (e) => {
              const response = await findZipCode(e);
              if (response) {
                handleChangeInputZipCode(response);
              }
            }}
            maxLength="10"
            minLength="1"
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="address">
            Endereo <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="address"
            title="Endereo"
            placeholder="Ex: Av. So Paulo"
            value={inputMember.address}
            onChange={handleChangeInput}
            maxLength="17"
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="district">
            Bairro <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="district"
            title="Bairro"
            placeholder="Ex: Centro"
            value={inputMember.district}
            onChange={handleChangeInput}
            maxLength="17"
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="number">
            Nmero <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="number"
            name="number"
            title="Nmero"
            placeholder="Ex: 123"
            value={inputMember.number}
            onChange={handleChangeInput}
            max="2147483647"
            min="0"
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="city">
            Cidade <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="city"
            title="Cidade"
            placeholder="Ex: So Paulo"
            value={inputMember.city}
            onChange={handleChangeInput}
            maxLength="17"
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="state">
            Estado <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputMember.state}
            title="Estado"
            name="state"
            id="state"
            required
          >
            <option value="AC">Acre</option>
            <option value="AL">Alagoas</option>
            <option value="AP">Amap</option>
            <option value="AM">Amazonas</option>
            <option value="BA">Bahia</option>
            <option value="CE">Cear</option>
            <option value="DF">Distrito Federal</option>
            <option value="ES">Esprito Santo</option>
            <option value="GO">Gois</option>
            <option value="MA">Maranho</option>
            <option value="MT">Mato Grosso</option>
            <option value="MS">Mato Grosso do Sul</option>
            <option value="MG">Minas Gerais</option>
            <option value="PA">Par</option>
            <option value="PB">Paraba</option>
            <option value="PR">Paran</option>
            <option value="PE">Pernambuco</option>
            <option value="PI">Piau</option>
            <option value="RJ">Rio de Janeiro</option>
            <option value="RN">Rio Grande do Norte</option>
            <option value="RS">Rio Grande do Sul</option>
            <option value="RO">Rondnia</option>
            <option value="RR">Roraima</option>
            <option value="SC">Santa Catarina</option>
            <option value="SP">So Paulo</option>
            <option value="SE">Sergipe</option>
            <option value="TO">Tocantins</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="country">
            Pas
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            id="country"
            name="country"
            title="Pas"
            placeholder="Ex: Brasil"
            value={inputMember.country}
            onChange={handleChangeInput}
            maxLength="17"
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="year_residence">
            Anos que reside na regio{" "}
            <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="number"
            name="year_residence"
            title="Anos que reside na regio"
            placeholder="Ex: 6"
            value={inputMember.year_residence}
            onChange={handleChangeInput}
            max="2147483647"
            min="0"
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="concession_validity">
            Validade da Concesso
          </label>
          <Input
            className="form-control-alternative"
            id="concession_validity"
            type="date"
            name="concession_validity"
            title="Validade da Concesso"
            value={inputMember.concession_validity}
            onChange={handleChangeInput}
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="always_resided">
            Sempre residiu no meio rural?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputMember.always_resided}
            title="Sempre residiu no meio rural?"
            name="always_resided"
            id="always_resided"
          >
            <option value="" hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label
            className="form-control-label"
            htmlFor="beneficiary_knows_limit"
          >
            O beneficirio conhece o limite do lote?{" "}
            <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputMember.beneficiary_knows_limit}
            title="O beneficirio conhece o limite do lote?"
            name="beneficiary_knows_limit"
            id="beneficiary_knows_limit"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="lot_has_marking">
            Lote tem marco que identifica os limites?{" "}
            <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputMember.lot_has_marking}
            title="Lote tem marco que identifica os limites?"
            name="lot_has_marking"
            id="lot_has_marking"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="has_contract">
            O beneficirio tem o contrato/termo de concesso de uso?{" "}
            <small className="text-red">(obrigatrio)</small>
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputMember.has_contract}
            title="O beneficirio tem o contrato/termo de concesso de uso?"
            name="has_contract"
            id="has_contract"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      </Row>
      </Col>
    </Row>
  );
};

export default Member;
